:root {
  --main-accent: #060b35;
  --secondary: #616070;
  --blue-accent: #00acdc;
  --white-accent: #fff;
}

body {
  box-sizing: border-box;
  width: 200px;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding-block: 32px 16px;
  padding-inline: 16px;
}

.card__imac {
  justify-content: center;
  width: 100%;
  display: flex;
}

.card__imac-img {
  background-image: url("imac.202c52ff.jpeg");
  background-size: 100% 100%;
  width: 160px;
  height: 134px;
}

.card__title {
  color: var(--main-accent);
  margin-block: 40px 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: var(--secondary);
  margin-block: 4px 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 8px;
  display: flex;
}

.card__price-text {
  color: var(--secondary);
  align-items: center;
  font-size: 12px;
  line-height: 18px;
}

.card__price-sum {
  color: var(--main-accent);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: var(--white-accent);
  text-transform: uppercase;
  background: var(--blue-accent);
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-weight: 800;
}

.card__button:hover {
  color: var(--blue-accent);
  background: var(--white-accent);
  border: 1px solid var(--blue-accent);
}

.stars {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__reviews {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars--0 .stars__star:nth-child(n+1), .stars--1 .stars__star:nth-child(n+2), .stars--2 .stars__star:nth-child(n+3), .stars--3 .stars__star:nth-child(n+4), .stars--4 .stars__star:nth-child(n+5) {
  background-image: url("star.822d561e.svg");
}

